body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    height: 100vh;
    padding: 0;
    background-color: #000;
}

section {
    height: 100vh;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

h1 {
    font-size: 5rem;
    color: white;
    position: absolute;
    // transform: scale(2);
    // top: 50%;
    // width: 100%;
    // text-align: center;
    z-index: 100;
    transform: translateY(-50%);
    // display: block;
}

.box {
    background: red;
    width: 200px;
    height: 200px;
}

canvas {
    width: 100%;
    height: 100%;
    margin: 0;
    display: block;
    position: relative;
    filter: brightness(50%);
}